import { z } from 'zod';
import { RelationTypesEnum } from '../type-enums';

export const RelationSchema = z
  .object({
    type: z.nativeEnum(RelationTypesEnum),
    dimension: z.string().min(1).optional(),
    targetDataset: z.string().min(1),
    targetDimension: z.string().min(1).optional(),
    sql: z.string().min(1).optional(),
  })
  .superRefine((val, ctx) => {
    if (
      val.sql !== undefined &&
      (val.dimension !== undefined || val.targetDimension !== undefined)
    ) {
      ctx.addIssue({
        code: z.ZodIssueCode.custom,
        message: 'Use sql field or dimension/targetDimension.',
      });
    }
  });

export interface RelationInterface {
  type: RelationTypesEnum;
  dimension?: string;
  targetDataset?: string;
  targetDimension?: string;
  sql?: string;
}
